import React, { Component } from "react";
import Card from "../components/layout/Card";
import Footer from "../components/layout/Footer";

// For performing client side validation, I have taken help of tutorial on this youtube video.
// The corresponding code of this video is present in a Github repository. I have provided that repositoriy's link as well
// https://www.youtube.com/watch?v=FM2RN8rHCTE
// https://github.com/benawad/practical-react/blob/11_validate_form/src/components/ValidationForm.js
//
const startState = {
  email: "",
  emailError: "",
  password: "",
  passwordError: "",
};
export class Login extends Component {
  state = startState;
  // validate = () => {
  //   let passwordError = "";
  //   let emailError = "";

  //   if (!this.state.email.includes("@")) {
  //     emailError = "Invalid Email";
  //   }

  //   if (emailError) {
  //     this.setState({ emailError });
  //     return false;
  //   }
  //   return true;
  // };
  handleChange = (event) => {
    const isCheckbox = event.target.type === "checkbox";
    this.setState({
      [event.target.name]: isCheckbox
        ? event.target.checked
        : event.target.value,
    });
  };

  validate = () => {
    let emailError = "";
    // let passwordError = "";

    if (!this.state.email) {
      emailError = "Please enter an email.";
    }

    if (!this.state.email.includes("@")) {
      emailError = "Invalid email! Try again.";
    }

    if (emailError) {
      this.setState({ emailError });
      return false;
    }

    return true;
  };

  handleSubmit = (event) => {
    event.preventDefault();
    const isValid = this.validate();
    if (isValid) {
      console.log(this.state);
      this.setState(startState);
    }
  };
  render() {
    return (
      <div>
        <div>
          <div className="container">
            <div className="row">
              <div className="card-panel col s6 offset-s3 hoverable blue-grey lighten-5">
                <form className="login-form" onSubmit={this.handleSubmit}>
                  <div className="row">
                    <div className="input-field col s12 center">
                      <p className="center login-form-text grey-text text-darken-2">
                        Please enter your email and password to login
                      </p>
                    </div>
                  </div>
                  <div className="row">
                    <div className="col s6 offset-s1">
                      <p className="red-text">Fields marked * are required.</p>
                    </div>
                  </div>
                  <div className="row">
                    <div className="input-field col s11 ">
                      <i className="mdi-social-person-outline prefix"></i>
                      <input
                        id="email"
                        type="text"
                        name="email"
                        value={this.state.email}
                        onChange={this.handleChange}
                        required
                      />
                      <div className="row">
                        <div className="red-text col s6 offset-s1">
                          {this.state.emailError}
                        </div>
                      </div>
                      <label for="email" className="center-align">
                        *Email
                      </label>
                    </div>
                  </div>
                  <div className="row margin">
                    <div className="input-field col s11">
                      <i className="mdi-action-lock-outline prefix"></i>
                      <input
                        id="password"
                        type="password"
                        name="password"
                        value={this.state.password}
                        onChange={this.handleChange}
                        required
                      />
                      <div className="red-text">{this.state.passwordError}</div>
                      <label for="password">*Password</label>
                    </div>
                  </div>

                  <div className="row">
                    <div className="input-field col s2 offset-s5">
                      <button
                        type="submit"
                        className="btn waves-effect waves-dark hoverable col s20"
                      >
                        Login
                      </button>
                    </div>
                  </div>
                  <div className="row">
                    <div className="input-field col s5 offset-s2">
                      <p className="margin left-align medium-small">
                        <a href="/register">Register Now!</a>
                      </p>
                      <p className="margin left-align medium-small">
                        <a href="page-forgot-password.html">
                          Forgot password ?
                        </a>
                      </p>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}

export default Login;
